import { Component, OnDestroy, OnInit } from '@angular/core';
import { PlatformService } from 'src/app/services/platform.service';
import { environment } from 'src/environments/environment';
import { Store } from '@ngxs/store';
import { SetParticipantData } from 'src/app/store/actions/participant.actions';
import { LanguageCode } from 'src/app/enums/language';
import { Router } from '@angular/router';
import { LinkService } from 'src/app/services/link.service';
import {MenuItemOptions} from '../../interfaces/content';

@Component({
  selector: 'app-login',
  styleUrls: ['./login.page.scss'],
  templateUrl: './login.page.html',
})
export class LoginPage implements OnInit, OnDestroy {
  renderWebview: boolean;
  renderAppview: boolean;

  constructor(
    private linkService: LinkService,
    private platform: PlatformService,
    private store: Store,
    private router: Router
  ) {}

  ngOnInit() {
    this.setApplicationView();
  }

  devLogin(participantId: string) {
    if (!participantId) return;
    const prefLang: LanguageCode = this.detectPreferredLanguage();
    this.store.dispatch(
      new SetParticipantData({
        participantId,
        sssoId: 'random',
        languageCode: prefLang,
      })
    );
    this.router.navigate(['/', 'home']);
  }

  async signIn(participantId: string) {
    if (participantId) { return(this.devLogin(participantId)); }
    const params = {
      apiKey: environment.apiKey,
      ReturnUrl: this.platform.isWeb()
        ? environment.callbackUrl.web
        : environment.callbackUrl.app,
    };
    const queryString = Object.entries(params)
      .map(([key, value]) => `${key}=${value}`)
      .join('&');
    const url = `${environment.authServer.login}?${queryString}`;
    const options: MenuItemOptions = {external: false, attributes: {target: '_self'}};
    this.linkService.openUrl(url, options);
  }

  setApplicationView(): void {
    this.renderWebview = this.platform.isWeb();
    this.renderAppview = !this.renderWebview;
  }

  detectPreferredLanguage(): LanguageCode{
    if(/^nl\b/.test(navigator.language)){
      return LanguageCode.NL;
    } else {
      return LanguageCode.EN;
    }
  }

  ngOnDestroy() {}
}
